@import './features.scss';


#navbar {
    display: flex;
    width: 100vw;
    z-index: 5;
    position: sticky;
    top: 0px;

    // border-bottom: 1px solid green;
    // flex-direction: column;
    >a {
        text-decoration: none;

        color: black;
        margin: 10px 20px;
        height: 100%;
    }
}

.navLeft {
    width: 40%;
    display: flex;
    // border: 1px solid red;
    align-items: center;
    justify-content: center;
    align-content: center;
    justify-content: center;
    font-size: 24px;
}

.navRight {
    display: flex;
    align-items: center;
    // border: 1px solid green;
    width: 100%;

    >.navBtns {
        display: flex;
        flex-direction: row;
        // gap: 20px;
        width: 100%;
        justify-content: right;
        align-items: center;
        padding-right: 30px;



        >a {
            text-decoration: none;

            color: black;
            margin: 10px 20px;
            height: 100%;

        }

    }
}

#loginSection {
    @include d-flex(flex, column, center, center);

    >h3 {
        text-align: center
    }

    >a {
        text-decoration: none;

        color: black;
    }

    >.form {
        @include d-flex(flex, column, center, center);
        gap: 10px;

        >input {
            height: 20px;
            text-align: center;
            border-radius: 15px;
            border: 1px solid grey;
            height: 30px;

        }

        >button {
            color: white;
            background-color: rgb(15, 199, 15);
            border: 1px solid transparent;
            border-radius: 20px;
            width: 90%;
            height: 35px;
        }
    }
}

#registerSection {

    >.form {
        @include d-flex(flex, column, center, center);
        // border: 1px solid red;
        // flex-direction: column;
        gap: 10px;

        >input {
            width: 70%;
            height: 20px;
            // border: 1px solid grey;
            text-align: center;
            border-radius: 15px;
            border: 1px solid grey;
            height: 30px;

        }

        >input:nth-child(4) {
            color: white;
            background-color: rgb(15, 199, 15);
            border: 1px solid transparent;
        }
    }
}


.hometop {

    width: 100vw;
    display: flex;
    justify-content: center;

    >form {
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 12px;

        >input {
            height: 30px;
            border-radius: 12px;
            border: 1px solid grey;
            text-align: center;
        }

        >input:nth-child(2) {
            height: 70px;
            border-radius: 12px;
            border: 1px solid grey;
            text-align: center;
        }

        >button {
            border: 1px solid grey;
            border-radius: 12px;
            height: 30px;
            background-color: chocolate;
            color: white;
        }
    }
}

.homebottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 30vh;
    // width: 50%;
    width: 100%;

    >.taskBox {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 60%;
        gap: 10px;
        // border: 1px solid red;
        min-height: 30vh;
        margin-top: 10px;

        >.task {
            min-height: 100px;
            // border: 1px solid red;
            display: flex;
            flex-direction: row;
            width: 100%;
            transition: all 0.3s ease;
            border: 1px solid grey;
            border-radius: 5px;

            >.taskBoxLeft {
                width: 70%;
                display: flex;
                // justify-content: center;
                // border: 1px solid red;
                overflow-x: hidden;

                ::-webkit-scrollbar {
                    width: 10px;

                }

                flex-direction: column;

                >.title {
                    text-align: center;
                    align-content: center;
                    // padding: 0 50%;
                    font-weight: 700;
                    width: 100%;
                    text-transform: capitalize;
                    // border: 1px solid red;
                    // color: pink;
                }

                >.description {
                    padding: 0px 10px;
                    overflow-x: scroll;
                    min-height: 60px;
                    display: flex;
                    word-break: break-all;
                    flex-wrap: wrap;
                    // margin-top: 10px;
                    text-align: center;
                    justify-content: center;
                    padding-top: 15px;
                    // border: 1px solid red;
                    width:100%;
                    // z-index: 10;
                }
            }

            >.taskBoxRight {
                // border: 1px solid pink;
                width: 30%;
                display: flex;
                flex-direction: row;
                gap: 10px;
                justify-content: space-around;
                align-items: center;

                >input {
                    // width: 200px;
                    size: 30px;
                    transform: scale(2);
                }

                >.bin {
                    // width: 40px;
                    transform: scale(2);
                    margin: 0 20px;

                }
            }
        }
    }
}

.profile {
    display: flex;
    justify-content: center;
    gap: 10px;

    >.profileCard {
        display: flex;
        gap: 10px;
        flex-direction: column;
        align-items: center;


        >img {
            width: 140px;
            height: 180px;
            border-radius: 5px;
            size: 80px;
        }
    }
}

@keyframes loader {
    0% {
        transform: rotate(360deg);
    }

    100% {
        transform: rotate(0deg);

    }
}

.loadingPage {
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0px;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.596);
    display: flex;
    // flex-direction;
    justify-content: center;
    align-items: center;

    >.loaderText {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;

        // border: 1px solid red;
        // z-index: 6;
        // animation-duration: 1s;
        // animation-name: loader;
        // animation-fill-mode: forwards;
        // animation-iteration-count: infinite;
        // animation-direction: normal;
        >.loader {

            border-left: 1px solid red;
            border-top: 1px solid red;
            width: 80px;
            height: 80px;
            position: absolute;
            animation: 1s loader infinite ease;
            border-radius: 50%;
            // position: absolute;
            // border: 1px solid red;
            animation: 1s forwards loader infinite;
            // transform: translateX(100);
            // transform: translate(-100%, -100%);

        }
    }
}